.contact {
  background-color: #aa06010d;
  padding: 64px 0 92px 0; }
  .contact p {
    font-size: 20px;
    line-height: 30px; }
  .contact__container {
    max-width: 792px;
    margin: 0 auto; }
  .contact label,
  .contact textarea {
    display: block; }
  .contact label {
    margin-bottom: 4px; }
  .contact .form-item input,
  .contact .form-item textarea {
    width: 100%;
    font-size: 16px;
    line-height: 24px; }
  .contact .form-item input {
    margin-bottom: 16px;
    padding: 14px 0 14px 16px;
    background: transparent;
    border: 1px solid rgba(0, 0, 0, 0.5); }
  .contact .form-item textarea {
    margin-bottom: 16px;
    padding: 14px 0 100px 16px;
    background: transparent;
    border: 1px solid rgba(0, 0, 0, 0.5); }
  .contact .form-agree {
    margin-bottom: 42px; }
    .contact .form-agree input {
      margin-right: 4px; }
    .contact .form-agree label {
      display: inline; }
  .contact .form-button input {
    background: #aa0601;
    color: #fff;
    border: none;
    padding: 16px 58px 16px 58px;
    font-size: 16px;
    line-height: 24px; }
    .contact .form-button input:hover {
      cursor: pointer; }

